body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

body *::-moz-selection {
  background-color: #14afca;
  color: white;
}

body *::selection {
  background-color: #14afca;
  color: white;
}

#root {
  height: 100vh;
  -webkit-perspective: 1px;
          perspective: 1px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  overflow-y: scroll;
  overflow-x: hidden;
  background-color: rgba(0, 0, 0, 0.9);
}



